import React from 'react'
import card1 from '../images/elon_m.jpg'
import card2 from '../images/anne_m.jpg'
import card3 from '../images/ray_k.jpg'

export const ContentFuturos = () => {
  return (
    <div className="container">
        <div className="row row-cols-1 row-cols-md-3 g-4">
            <div className="col">
                <div className="card">
                <img src={card1} className="card-img-top" alt="..." height="250 px"/>
                <div className="card-body">
                    <h5 className="card-title">Elon Musk</h5>
                    <p className="card-text" style={{textAlign:"justify"}}>
                    <b>Contribucin:</b> Empresario y fundador de SpaceX y Tesla, entre otras compaas. Ha expresado inters y ha invertido en el desarrollo de tecnologas avanzadas, como la inteligencia artificial y la colonizacin de Marte.
                    <br/><b>Destacado por:</b> Su visin audaz en el avance de la tecnologa, incluidas las iniciativas para llevar a los humanos a Marte y sus preocupaciones sobre los riesgos de la inteligencia artificial.
                    </p>
                </div>
                </div>
            </div>
            <div className="col">
                <div className="card">
                <img src={card2} className="card-img-top" alt="..." height="250 px"/>
                <div className="card-body">
                    <h5 className="card-title">Anne-Marie Imafidon</h5>
                    <p className="card-text" style={{textAlign:"justify"}}>
                    <b>Contribucin:</b> Matemtica, cientfica informtica y defensora de la educacin en tecnologa. Fundadora de Stemettes, una organizacin que promueve la participacin de las mujeres en las disciplinas STEM.
                    <br/><b>Destacado por:</b> Su trabajo en la promocin de la diversidad y la inclusin en la tecnologa y la inspiracin de las futuras generaciones de mujeres en STEM.
                    </p>
                </div>
                </div>
            </div>
            <div className="col">
                <div className="card">
                <img src={card3} className="card-img-top" alt="..." height="250 px"/>
                <div className="card-body">
                    <h5 className="card-title">Ray Kurzweil</h5>
                    <p className="card-text" style={{textAlign:"justify"}}>
                    <b>Contribucin:</b> Inventor y futurista, conocido por sus predicciones sobre la convergencia de la tecnologa y la inteligencia artificial, as como por su trabajo en el desarrollo de tecnologas de reconocimiento de voz y lectura ptica de caracteres.
                    <br /><b>Destacado por:</b> Su visin del futuro tecnolgico y la idea de la "singularidad tecnolgica", un punto en el futuro en el que la inteligencia artificial superar la inteligencia humana.
                    </p>
                </div>
                </div>
            </div>
        </div>
    </div>
  )
}
