import React from 'react'
import logo from '../images/logooficial.png'
import { NavLink } from 'react-router-dom'

export const Header = () => {
  return (
    <>
        <header style={{color:"white"}}>
            <nav className="navbar navbar-expand-lg navbar-dark bg-dark">
                <NavLink to="/home">
                    <img src={logo} height="100" alt="No Encontrada" />
                </NavLink>

                <button type="button" className="navbar-toggler" data-bs-toggle="collapse" data-bs-target="#navbarCollapse">
                    <span className="navbar-toggler-icon"></span>
                </button>

                <div className="collapse navbar-collapse" id="navbarCollapse">
                    <div className="navbar-nav ms-auto">
                        <h4><NavLink to="/home" className="nav-item nav-link text-center" style={{color:"white"}}>Inicio</NavLink></h4>
                        <h4><NavLink to="/galeria" className="nav-item nav-link text-center" style={{color:"white"}}>Galeria</NavLink></h4>
                        <h4><NavLink to="/doctores" className="nav-item nav-link text-center" style={{color:"white"}}>Pioneros</NavLink></h4>
                        <h4><NavLink to="/mujeres" className="nav-item nav-link text-center" style={{color:"white"}}>Mujeres</NavLink></h4>
                        <h4><NavLink to="/revolucionarios" className="nav-item nav-link text-center" style={{color:"white"}}>Revolucionarios</NavLink></h4>
                        <h4><NavLink to="/cienciaDatos" className="nav-item nav-link text-center" style={{color:"white"}}>Ciencia de Datos</NavLink></h4>
                        <h4><NavLink to="/inteligenciaArtificial" className="nav-item nav-link text-center" style={{color:"white"}}>I.A.</NavLink></h4>
                        <h4><NavLink to="/futuros" className="nav-item nav-link text-center" style={{color:"white"}}>Futuro</NavLink></h4>
                        <h4><NavLink to="/mensajes" className="nav-item nav-link text-center" style={{color:"white"}}>Sugerencias</NavLink></h4>
                        <h4><NavLink to="/acercade" className="nav-item nav-link text-center" style={{color:"white"}}>Credito</NavLink></h4>
                    </div>
                </div>

            </nav>
        </header>
    </>
  )
}
