import React from 'react'
import card1 from '../images/margaret_h.jpg'
import card2 from '../images/barbara_l.jpg'
import card3 from '../images/radia_p.jpg'

export const ContentMujeres = () => {
  return (
    <div className="container">
        <div className="row row-cols-1 row-cols-md-3 g-4">
            <div className="col">
                <div className="card">
                <img src={card1} className="card-img-top" alt="..." height="250 px"/>
                <div className="card-body">
                    <h5 className="card-title">Margaret Hamilton</h5>
                    <p className="card-text" style={{textAlign:"justify"}}>
                    <b>Contribucin:</b> Pionera en la ciencia de la computacin y directora de la Divisin de Ingeniera de Software del Laboratorio de Instrumentacin del MIT. Desarroll el software de vuelo para las misiones Apolo de la NASA.
                    <br/><b>Destacado por:</b> Su liderazgo en el diseo del software de vuelo crtico para el alunizaje y su contribucin al establecimiento de la ingeniera de software como disciplina.
                    </p>
                </div>
                </div>
            </div>
            <div className="col">
                <div className="card">
                <img src={card2} className="card-img-top" alt="..." height="250 px"/>
                <div className="card-body">
                    <h5 className="card-title">Barbara Liskov</h5>
                    <p className="card-text" style={{textAlign:"justify"}}>
                    <b>Contribucin:</b> Informtica y profesora, conocida por sus contribuciones a la programacin orientada a objetos y la creacin del principio de sustitucin de Liskov.
                    <br/><b>Destacado por:</b> Ser la primera mujer en obtener un doctorado en ciencias de la computacin y por su impacto en el diseo de lenguajes de programacin.
                    </p>
                </div>
                </div>
            </div>
            <div className="col">
                <div className="card">
                <img src={card3} className="card-img-top" alt="..." height="250 px"/>
                <div className="card-body">
                    <h5 className="card-title">Radia Perlman</h5>
                    <p className="card-text" style={{textAlign:"justify"}}>
                     <b>Contribucin:</b> Ingeniera de redes y programadora, conocida por sus contribuciones al desarrollo de los protocolos de red y por inventar el algoritmo Spanning Tree.
                    <br/><b>Destacado por:</b> Su papel fundamental en la arquitectura de redes y su enfoque en la resiliencia y la redundancia en las comunicaciones en red.
                    </p>
                </div>
                </div>
            </div>
        </div>
    </div>
  )
}
